}
#GSTM-app form .React_select__input {
  min-height: 1px;
}
#GSTM-app form .React_select__dropdown-indicator {
  min-height: 1px;
  padding-top: 0;
  padding-bottom: 0;
}
#GSTM-app form .React_select__indicator-separator {
  min-height: 1px;
}
#GSTM-app form .React_select__indicator-container {
  min-height: 1px;
}
#GSTM-app form .React_select__dropdown-indicator {
  min-height: 1px;
}
#GSTM-app form .React_select__clear-indicator {
